import IconAvatar from '@/assets/svg/IconAvatar'
import IconGlobal from '@/assets/svg/IconGlobal'
import IconMenu from '@/assets/svg/IconMenu'
import React, { memo, useEffect, useState } from 'react'
import RightWrapper from './sstyle'

const Right = memo(() => {
	const [showPanel, setShowPanel] = useState(false)

	useEffect(() => {
		function windowClickHandle() {
			setShowPanel(false)
		}
		window.addEventListener('click', windowClickHandle, true) // 
		return () => {
			window.removeEventListener('click', windowClickHandle, true)
		}
	}, [])

	const onProfileClick = () => {
		setShowPanel(true)
	}

	return (
		<RightWrapper>
			<div className="btns">
				<span className="btn"></span>
				<span className="btn"></span>
				<span className="btn">
					<IconGlobal />
				</span>
			</div>

			<div className="profile" onClick={onProfileClick}>
				<IconMenu />
				<IconAvatar />

				{ showPanel && (
					<div className="panel">
						<div className="top">
							<div className="item register"></div>
							<div className="item"></div>
						</div>
						<div className="bottom">
							<div className="item"></div>
							<div className="item"></div>
							<div className="item"></div>
						</div>
					</div>
				) }

			</div>
		</RightWrapper>
	)
})

export default Right